
import { defineComponent, SetupContext } from "vue";
export default defineComponent({
  name: "Modal",
  props: {
    bookings: {
      type: Object,
    },
    isShow: Boolean,
  },
  emits: ["handleClose"],
  setup(props, context: SetupContext) {
    const closeModal = () => {
      context.emit("close");
    };
    return {
      closeModal,
    };
  },
});
